* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: 'montserrat', sans-serif;
}

#root {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  --primary-white: #fff;
  --secundary-white: #F5F5F5;
  --primary-black: #333333;
  --secundary-black: #000;
}

body {
  font-family: sans-serif;
  margin: 0px;
  padding: 0px;
  background-color: #333;
}

h1 {
  font-size: 22px;
}

h2 {
  font-size: 20px;
}

h3 {
  font-size: 18px;
}

h4 {
  font-size: 16px;
}

h5 {
  font-size: 14px;
}

h6 {
  font-size: 12px;
}

code {
  font-size: 1.2em;
}

.divNav {
  height: 50px;
  width: 100%;
}
.navigationApp {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 50px;
  padding: 0px 16px;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  color: var(--primary-white);
  background-color: var(--primary-black);
  
  position: fixed;
  z-index: 2;
}
.searchApp {
  position: fixed;
  z-index: 3;
  top: 15px;
  border: none;
  border-radius: 8px;
  padding: 2px 4px;
}

.sectionApp {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1px;
  width: 100%;
  justify-items: center;
}


.productApp {
  display: -ms-flexbox;
  display: flex;
  position: relative;
  width: 100%;
  height: 25vh;
  background-color: #D9D9D9;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.productApp .imageProduct{
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  position: absolute;
  opacity: 1;
}
.etiqueta {
  height: 14px;
  display: -ms-flexbox;
  display: flex;
  padding: 0px 4px;
  margin-top: 8px;
  background-color: #FFC107;
  font-size: 11px;
  font-weight: bold;
  border-radius: 4px;
  z-index: 1;
  -ms-flex-align: center;
      align-items: center;
  gap: 8px;
}
.etiqueta p, .etiqueta span {
  margin: 0; 
  z-index: 1;
  color: var(--secundary-black);
}

.productApp a {
  position: absolute;
  bottom: 0;
  width: 100%;
}
.productApp button {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  gap: 8px;
  -ms-flex-direction: row;
      flex-direction: row;
  bottom: 0;
  width: 100%;
  background: #25D366;
  color: var(--primary-white);
  border: none;
  opacity: 1;
  font-weight: bold;
  font-size: 14px;
}
.productApp button:active {
  -webkit-transform: translateY(2px);
      -ms-transform: translateY(2px);
          transform: translateY(2px);
  -webkit-filter: contrast(80%);
          filter: contrast(80%);
}
.productApp button:hover {
  cursor: pointer;
}

.exitProduct {
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  -ms-flex-pack: center;
      justify-content: center;
  top: 0px;
  right: 0px;
  width: 100%;
  height: 100%;
  z-index: 4;
  -webkit-backdrop-filter: blur(0.8px);
          backdrop-filter: blur(0.8px);
}
.productAbout {
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  width: 80%;
  height: 40%;
  top: 25vh;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  z-index: 2;
  -webkit-filter: blur(0px);
          filter: blur(0px);
}
.etiquetaAbout {
  height: 22px;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  padding: 0px 4px;
  top: 8px;
  right: 0px;
  background-color: #FFC107;
  font-size: 11px;
  font-weight: bold;
  border-radius: 8px 0px 0px 8px;
  z-index: 1;
  -ms-flex-align: center;
      align-items: center;
  gap: 8px;
}
.etiquetaAbout p, .etiquetaAbout span {
  margin: 0; 
  z-index: 1;
  color: var(--secundary-black);
}

.imageProductAbout {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  border-radius: 8px 8px 0px 0px;
}
  
.productAbout a button {
  height: 50px;
  top: 0px;
  border-radius: 0px 0px 8px 8px;
}
.iconWhatsapp {
  width: 18px;
  height: 18px;
  background-image: url('./public/whatsapp.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

@media (min-width: 501px) {
  .searchApp {
    width: 70%;
    top: 4%;
    padding: 0.4em;
    max-height: 35px;
  }
  .navigationApp {
    height: 11%;
  }
  
  .sectionApp {
    grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  }
}



